import React, { useReducer } from "react";
import styled from "styled-components";
import arrow1 from "./arrow.png";

const initialState = {
  translateX: "0%",
  transition: "0.4s",
  i: 0,
};

const reducer = (state, action) => {
  switch (action.type) {
    case "increment":
      return {
        ...state,
        i: state.i + 1,
        translateX: `${-100 * (state.i + 1)}%`,
        transition: "0.4s",
      };
    case "jump":
      return {
        ...state,
        i: action.payload,
        translateX: `${-100 * action.payload}%`,
        transition: "0.4s",
      };
    case "resetFirst":
      return {
        ...state,
        i: 0,
        translateX: "0%",
      };
    case "resetLast":
      return {
        ...state,
        i: action.payload.length - 1,
        translateX: `${-100 * (action.payload.length - 1)}%`,
        // transition: "0",
      };

    case "decrement":
      // console.log(state.i);

      return {
        ...state,
        i: state.i - 1,
        translateX: `${-100 * (state.i - 1)}%`,
        transition: "0.4s",
      };

    default:
      return state;
  }
};

export const Carousel = ({ images }) => {
  const [state, dispatch] = useReducer(reducer, initialState);
  // const [translateX, setTranslateX] = useState("-100%");
  // const [transition, setTransition] = useState("1s");
  // const [i, setI] = useState(2);

  //   const first = images[0];
  //   const last = images[images.length - 1];
  //   const lists = images.length >= 2 ? [last].concat(images, first) : images;

  const imagess = images.map((image, i) => (
    <img className={`img-${i}`} src={image} key={image + i}></img>
  ));
  // const dots = images.map((image, i) => <input key={i} type="radio"></input>);
  const names = [
    "Lemon Passionfruit Cake",
    "Citrus Mint Cake",
    "Whole Boba Cake",
  ];
  const dots = names.map((name, i) => {
    return (
      <div className={`clickBox click${i}`} id={`${i}`}>
        <div className={`dot dot${i} click`} key={i} id={`${i}`}></div>
        <div className="click123" id={`${i}`}>
          {name}
        </div>
      </div>
    );
  });
  const slideLeft = (e) => {
    // console.log(e);
    // if (state.i === images.length + 1) return;

    dispatch({ type: "increment" });

    if (state.i === images.length - 1) {
      setTimeout(() => {
        dispatch({ type: "resetFirst" });
      }, 0);
    }
  };

  const slideRight = (e) => {
    // console.log(e);
    // if (state.i === 0) return;
    dispatch({ type: "decrement" });
    if (state.i === 0) {
      setTimeout(() => {
        dispatch({ type: "resetLast", payload: images });
      }, 0);
    }
  };

  const jumpToImage = (e) => {
    //TODO:
    const target = e.target;
    if (!target.className.includes("click")) return;
    // if () return;
    // console.log(target.className);
    // console.log(target.className.includes("dot dot"));
    dispatch({ type: "jump", payload: +target.id });
  };
  const infos = [
    {
      title: "6'/8' Lemon Passionfruit Cake",
      info:
        "Layered with Passionfruit mousse, Passionfruit crème de chantilly and lemon crème de chantilly. Honey lemon chiffon cake ",
    },
    {
      title: "6'/8' Citrus Mint Cake",
      info:
        "Best eaten within 2-4 hours. To store, the cake should be refrigerated while the bubbles remain at room temp.",
    },
    {
      title: "6'/8' Whole Boba Cake",
      info:
        "4' is not enough? Try the 6' or 8' then!\n\n6' is for roughly 4-5 people\n\n8' is for roughly 6-8 people.\n\nEarl grey chiffon, earl grey infused cream, Assam milk tea panna cotta and perfectly cooked black sugar bubbles\n\nPlease inform us the pickup time so we can prepare the freshest bubbles for you. Best eaten within 2-5 hours after pickup.\n\nPlease allow at least 2 days notice as we prepare everything fresh.\n\n*Contains milk, cream and eggs. May contains sesame, nuts and peanuts.",
    },
  ];
  const info = infos.map((info, i) => {
    return (
      <div className={`info info${i}`} key={i}>
        <h2>{info.title}</h2>
        <h3>{info.info}</h3>
      </div>
    );
  });

  const ingredients = [];

  const ingredient = ingredients.map((ingredient, i) => {
    return (
      <div className={`ig ig${i}`} key={i}>
        <img src={arrow1} />
        <h4>{ingredient}</h4>
      </div>
    );
  });

  return (
    <Slider
      translateX={state.translateX}
      transition={state.transition}
      i={state.i}
    >
      {imagess}
      <div className="arrows">
        <i onClick={slideLeft} className="fas fa-chevron-right fa-2x"></i>
        <i onClick={slideRight} className="fas fa-chevron-left fa-2x"></i>
      </div>
      <div onClick={jumpToImage} className="dotsBox">
        {dots}
      </div>
      <div className="infoBox">{info}</div>
      <div className="ingredientBox">{ingredient}</div>
    </Slider>
  );
};

